.--img-rapper {
  position: relative;
  background: #333;
  width: 100%;
  height: 350px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 3rem 0;
}

.--img-rapper img {
  width: 100%;
  height: 100%;
}

.--photo-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 0.5rem;
}
.--photo-item {
  background: #333;
  width: 48%;
  height: 175px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.--photo-item img {
  width: 100%;
  height: 100%;
}

.--compCard-img {
  max-width: 100%;
  max-height: 200px;
}
.--note-text-rapper {
  display: flex;
  flex-direction: column;
  gap: 1.8rem;
}

.--upload-btn {
  position: absolute;
  width: 70px;
  height: 70px;
  border: none;
  outline: none;
  background: var(--Light-color);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.--upload-btn:hover {
  background: gray;
  color: var(--Colored-color);
}

@media screen and (max-width: 340px) {
  .--img-rapper {
    height: 320px;
  }
  .--photo-item {
    height: 170px;
  }
  .--compCard-img {
    max-width: 100%;
    max-height: 180px;
  }
}
@media screen and (min-width: 768px) {
  .--img-rapper {
    height: 400px;
  }
  .--img-rapper img {
    width: 80%;
  }
  .--photo-list {
    gap: 1rem;
  }
  .--photo-item {
    width: 30%;
    height: 175px;
  }
  .--compCard-img {
    max-width: 500px;
    max-height: 300px;
  }
  .--upload-btn {
    width: 100px;
    height: 100px;
  }
}

@media screen and (min-width: 920px) {
  .--detail-rapper {
    display: flex;
    flex-direction: row-reverse;
    gap: 1rem;
  }
  .--img-rapper {
    width: 50%;
    margin-top: 0rem;
  }
  .--img-rapper img {
    width: 100%;
  }
  .--note-text-rapper {
    width: 50%;
    margin-top: 1.5rem;
  }

  .--photo-item {
    width: 30%;
    height: 270px;
  }
}
@media screen and (min-width: 920px) and (min-height: 850px) {
  .--img-rapper {
    height: 550px;
    width: 100%;
    margin-top: 3rem;
  }
  .--img-rapper img {
    width: 80%;
  }
  .--detail-rapper {
    display: block;
  }
  .--note-text-rapper {
    width: 100%;
    margin-top: 0;
  }
  .--photo-item {
    height: 250px;
  }
}

@media screen and (min-width: 1280px) {
  .--img-rapper img {
    width: 85%;
  }
}
